 equidad que aseguren la libre competencia. El Comité investiga las tendencias de inversiones sectoriales y entrega información de interés para orientar a las empresas del sector, principalmente en el logro de la calidad de las obras, cumplimientos de los plazos y de la seguridad de sus trabajadores.")])]),_c('modal',{attrs:{"name":"obras-infra","height":400}},[_c('a',{staticClass:"close-modal-text",on:{"click":function($event){return _vm.$modal.hide('obras-infra')}}},[_vm._v("X")]),_c('p',[_vm._v("Agrupa a empresas de construcción, de ingeniería y de consultoría relacionadas con los contratos de obras de infraestructura de uso público (carreteras, puentes, obras hidráulicas, arquitectura, concesiones, obras sanitarias, ferroviarias, etc). Su labor está orientada al estudio de las inversiones proyectadas por el MOP y otros mandantes de infraestructura de uso público. Analiza las condiciones generales de contratación buscando relaciones contractuales que favorezcan la equidad, transparencia y la incorporación de innovación tecnológica en los procesos constructivos. Mantiene una permanente relación con los diferentes mandantes de infraestructura de uso público (MOP, Empresas Sanitarias, Metro S.A., entre otras).")])]),_c('modal',{attrs:{"name":"concesiones","height":400}},[_c('a',{staticClass:"close-modal-text",on:{"click":function($event){return _vm.$modal.hide('concesiones')}}},[_vm._v("X")]),_c('p',[_vm._v("Agrupa a las empresas concesionarias de obras de infraestructura de uso público y de servicios, con el objeto de representar los intereses de las grandes, medianas y pequeñas concesionarias que operan en este ámbito. Sus principales líneas de acción contemplan el perfeccionamiento de la institucionalidad de las concesiones y la ampliación de su ámbito de aplicación; la participación en el debate formativo que afecta a este sector; procurar agilidad en la tramitación de los decretos y en el cumplimiento de los calendarios de licitaciones, así como el desarrollo d